
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default {
components: {
Group,
Cell,
XButton,
Flexbox,
FlexboxItem
},
data () {
return {
payConfig: {},
type: this.$store.getters.getType,
showScrollBox: false,
pageInfo: '',
resInfo: {}
}
},
mounted () {
window.addEventListener('popstate', (e) => {
if (this.type === 'wx') {
this.$wechat.closeWindow()
} else if (this.type === 'alipay') {
window.AlipayJSBridge.call('closeWebview')
}
}, false)
},
computed: {
goodsItems () {
let items = this.$store.getters.getGoodItems
let datas = []
for (let i = 0; i < items.length; i++) {
let item = items[i]
item.totalPrice = '￥' + item.gPrice * item.gCount
item.gDesc = '数量:' + item.gCount
datas.push(item)
}
return datas
},
totalPrice () {
return '￥' + this.$store.getters.getTotalPrice
},
goodsCounts () {
let count = 0
let items = this.goodsItems
for (let i = 0; i < items.length; i++) {
let item = items[i]
count += item.gCount
}
return count
},
payBtnInfo () {
if (this.type === 'wx') {
return '微信支付'
} else if (this.type === 'alipay') {
return '支付宝支付'
}
},
alipayStyle () {
if (this.type === 'alipay') {
return {backgroundColor: '#0F8CE6'}
} else {
return {}
}
}
},
methods: {
pay () {
if (this.resInfo) {
let gInfos = []
let items = this.goodsItems
for (let i = 0; i < items.length; i++) {
let str = items[i].gId + '_' + items[i].gCount
gInfos.push(str)
}
let params = {
type: this.$store.getters.getType,
userName: this.$store.getters.getUserInfo.userId,
compId: this.$store.getters.getCompInfo.compId,
gInfo: gInfos
}
this.$api.pay(params).then(res => {
if (res && res.code === '0000' && res.msg) {
this.resInfo = res
this.goPay(res)
} else {
this.$vux.alert.show({
content: '操作失败'
})
setTimeout(() => {
this.$vux.alert.hide()
}, 3000)
}
}).catch(error => {
console.log(error)
})
} else {
this.goPay(this.resInfo)
}
},
goPay (res) {
if (this.type === 'wx') {
this.$wechat.chooseWXPay({
'timestamp': res.msg.timeStamp,
'nonceStr': res.msg.nonceStr,
'package': res.msg.package,
'signType': res.msg.signType,
'paySign': res.msg.paySign,
success: (res) => {
this.$router.push('payResult')
},
cancel: (res) => {
this.$vux.toast.text('取消了支付')
}
})
} else if (this.type === 'alipay') {
const div = document.createElement('div')
div.innerHTML = res.msg.a_page
document.body.appendChild(div)
document.forms[0].submit()
}
}
}
}



